import React from "react";
import classes from "./contact.module.css"
import { compose, withProps } from "recompose"
import { withScriptjs, withGoogleMap, GoogleMap } from "react-google-maps"

import Linkedinicon from "react-icons/lib/fa/linkedin-square";
import Giticon from "react-icons/lib/fa/git-square";
//const baseimgurl = process.env.PUBLIC_URL + "/assets/images/";

const MapComponent = compose(
  withProps({
    googleMapURL: "https://maps.googleapis.com/maps/api/js?key=AIzaSyDf-yIqxErTkbWzKhLox7nAANnrfDIY190&v=3.exp&libraries=geometry,drawing,places",
    loadingElement: <div style={{ height: `100%` }}></div>,
    containerElement:<div style={{height: `100%`}} />,
    mapElement: <div style={{ height: `100%` }} />,
  }),
  withScriptjs,
  withGoogleMap
  )(props => 
      <GoogleMap zoom={15} center={{lat:25.122848, lng:55.206631}}
        options={{ mapTypeControl: true, mapTypeControlOptions:{position:window.google.maps.ControlPosition.LEFT_BOTTOM}, streetViewControl: false}}
      >
      </GoogleMap>
  );

const Contact = props => (
  <div className={classes.Contact}>
    <h1 className={classes.Header}>Contact Me</h1>
    <div className={classes.Row}>
      <div
        data-version="v1"
        data-size="medium"
        data-locale="en_US"
        data-type="horizontal"
        data-theme="light"
        data-vanity="james-chan-a93bbb167"
      />
      <div className={classes.ContactInfo}>
        <h2>E-Mail : jameschan666000@gmail.com</h2>
        <h2>Mobile Phone : +971559220225</h2>
        <h2>Skype : live:myfivegoldstar888@gmail.com</h2>
        <h2>Address : Al Barsha First, Dubai, UAE</h2>
        <div>
          <div
            className={classes.Linkedin}
            onClick={() => {
              window.location =
                "https://ae.linkedin.com/in/james-chan-a93bbb167?trk=profile-badge";
            }}
          >
            <Linkedinicon color="#bbbbbb" size="40" />
          </div>
          <div
            className={classes.Github}
            onClick={() => {
              window.location = "https://github.com/procoder17";
            }}
          >
            <Giticon color="#bbbbbb" size="40" />
          </div>
        </div>
      </div>
      <div className={classes.Map}>
        <MapComponent />
      </div>
    </div>
  </div>
);

export default Contact;



